class taskmanage extends DataBase {
  complete() {
    this.taskType = window.location.hash.indexOf('taskmanage')!==-1?1:3
    this.changeAll(this.model.condition)
    this.addli(this.model.condition)
    this.model.tableconfig.tablewidth = ES.selctorDoc('.taskmanage').box().clientWidth - 240
    this.loadlist('group')
    this.dom.find('.strokeBtn').on('click', () => {
      this.app.session.set('ischanged', false);
      if(this.taskType == 1){
        this.app.changePage('createtask', { type: 'new' })
      }else{
        this.app.changePage('createbackflowtask1', { type: 'new' })
      }
    })
    this.dom.find('.fillBtn').on('click', () => {
      this.model.condition.forEach((val, idx) => {
        if (val[0].name == 'inspectTime') {
          this.model.apiData.startCreateTime = this.getfiltercondition()[val[0].name][0].startTime
          this.model.apiData.endCreateTime = this.getfiltercondition()[val[0].name][0].endTime
        } else {
          this.model.apiData[val[0].name] = this.getfiltercondition()[val[0].name].toString() == '' ? null : this.getfiltercondition()[val[0].name].toString()
        }
      })
      this.model.apiData.projectIdList = this.model.apiData.projectIdList ? this.model.apiData.projectIdList.split(',').map(Number) : null
      if (this.model.apiData.algPreAnnotation) {
        this.model.apiData.algPreAnnotation = this.model.apiData.algPreAnnotation == 1 ? true : false
      }
      this.model.apiData.taskIdList = this.model.apiData.taskIdList ? this.model.apiData.taskIdList.split(',').map(Number) : null
      this.model.apiData.vendorIdList = this.model.apiData.vendorIdList ? this.model.apiData.vendorIdList.split(',').map(Number) : null
      this.model.apiData.page = 1
      this.model.apiData.type=1
      this.search(true)
    })
  }
  async search(bool) {
    let data2 = []
      this.model.apiData.type = this.taskType
    let res = await this.api.queryTask(this.model.apiData)
    if (res.code == 0) {
      if (res.data.list.length > 0) {
        res.data.list.forEach((val, i) => {
          for (let i in val) {
            val[i] = val[i] == null ? '' : val[i]
          }
          let obj = {}
          val.vendors = ''
          obj.id = val.id + ',' + val.projectStatus + ',' + val.seriesTotalNum / (val.crossMarkNum ? val.crossMarkNum : 1) + ',' + val.projectId+','+val.status
          val.assignVendors = val.assignVendors.length > 0 ? val.assignVendors.toString() : ''
          val.remark = JSON.parse('"' + val.remark + '"')
          switch (val.status) {
            case 1:
              val.status = '待发布';
              obj.operation = this.model.listicon.action;
              break;
            case 2:
              val.status = '进行中';
              if (val.seriesAvailableNum === val.seriesTotalNum) {
                obj.operation = this.model.listicon.action1
              } else {
                obj.operation = this.model.listicon.action3
              }
              break;
            case 3:
              val.status = '已完成';
              if (val.seriesAvailableNum === val.seriesTotalNum) {
                obj.operation = this.model.listicon.action5
              } else {
                obj.operation = this.model.listicon.action4
              }
              break;
            case 4:
              val.status = '已终结';
              if (val.seriesAvailableNum === val.seriesTotalNum) {
                obj.operation = this.model.listicon.action5
              } else {
                obj.operation = this.model.listicon.action4
              }
              break;
          }
          data2.push(obj)
          val.method = val.method == 1 ? '承包式' : '开放式'
          val.algPreAnnotation = val.algPreAnnotation ? '算法标注' : '人工标注'
          val.projectStatus = val.projectStatus == 1 ? '未启用' : '已启用'
          val.crossMarkNum = val.assignMethod == 2 ? 1 : val.crossMarkNum
          val.seriesTotalNum = val.crossMarkNum ? val.seriesTotalNum / val.crossMarkNum + '(*' + val.crossMarkNum + ')' : val.seriesTotalNum + '(*1)'
          if (val.createTime) val.createTime = Tool.time(val.createTime, 'yyyy-mm-dd')
          if (val.startTime) val.startTime = Tool.time(val.startTime, 'yyyy-mm-dd')
          if (val.endTime) val.endTime = Tool.time(val.endTime, 'yyyy-mm-dd')
          val.isassign = '';
        })
        this.tablecont.setData(res.data.list, data2)
      } else {
        this.tablecont.noData();
      }
    }
    if (bool) {
      this.tablecont.getTotal(res.data.pages, 2, res.data.total)
    }
    this.initscroll();
  }

  async listaction(value) {
    let page = this.taskType===1?'createtask':'createbackflowtask1'
    switch (value.classname) {
      case 'view':
        // this.app.changePage(this.taskType===1?'createtask':'createbackflowtask1', {
        //   type: 'view',
        //   taskid: value.id.split(',')[0],
        //   status:value.id.split(',')[4]
        //   // taskid: value.id.split(',')[0]
        // })
        window.open('http://' + window.location.host + '/#!/' +page+ '/view/' + value.id.split(',')[0] +'//'+value.id.split(',')[4])
        break;
      case 'edit':
        // this.app.changePage(this.taskType===1?'createtask':'createbackflowtask1', {
        //   type: 'edit',
        //   taskid: value.id.split(',')[0],
        //   projectid: value.id.split(',')[3],
        //   status:value.id.split(',')[4]
        // })
        window.open('http://' + window.location.host + '/#!/' +page+ '/edit/' + value.id.split(',')[0] +'/'+value.id.split(',')[3]+'/'+value.id.split(',')[4])
        break;
      case 'copy':
        this.app.alert.show({
          title: ' ',
          msg: '确认复制吗？',
          close: true,
          sure: async ()=>{
            let copyRes = await this.api.task_clone({taskId: value.id.split(',')[0]})
            if(copyRes.code == 0){
              this.model.apiData.page = 1;
              this.search(true)
            }else{
              alert('复制失败')
            }
          }
        })
        break;
      case 'start':
        if (value.id.split(',')[1] == 1) {
          this.app.alert.show({
            title: ' ',
            msg: '此任务中引用的项目未启动，请先启动项目。',
            close: true
          })
        } else {
          this.app.alert.show({
            title: ' ',
            msg: '是否确定开始任务，本次任务的需要标注的序列数量为' + value.id.split(',')[2] + '个，一旦开始序列数量即为锁定。',
            close: true,
            sure: async () => {
              this.app.loading.show()
              let res = await this.api.startTask(parseInt(value.id.split(',')[0]))
              this.app.loading.hide()
              if (res.code == 0) {
                this.model.apiData.page = 1;
                this.search(true);
              } else {
                this.app.alert.show({
                  title: '提醒',
                  msg: res.msg,
                  close: true,
                })
              }
            }
          })
        }
        break;
      case 'end':
        this.app.alert.show({
          title: ' ',
          msg: '是否确定要终结任务，一旦终结外部医生的任务即结束，本次任务也无法再次发布。',
          close: true,
          sure:async () => {
            let res = await this.api.task_end({taskId:value.id.split(',')[0]})
            if(res.code==0){
              this.model.apiData.page = 1;
              this.search(true)
            }else{
              alert('操作失败')
            }
          }
        })
        break;
      case 'viewxl':
        this.app.changePage('ytjtaskdetail', {
          taskid: value.id.split(',')[0],
          taskType:this.taskType
        })
        break;
    }
  }
  toggletab(value){
    this.app.changePage(this.taskType ==1?'taskmanage':'backflowtask', { type: value.id })
  }
  getapidata(value) {
    if (value) {
      switch (value.name) {
        case 'taskIdList':
          this.chooseData.moduleobj.taskIdList.loading(true)
          this.taskNameSearch(value.data.data ? value.data.data.trim() : "");
          break;
        case 'projectIdList':
          this.chooseData.moduleobj.projectIdList.loading(true)
          this.projectNameSearch(value.data.data ? value.data.data.trim() : "");
          break;
        case 'vendorIdList':
          this.chooseData.moduleobj.vendorIdList.loading(true)
          this.taskVendorNameSearch(value.data.data ? value.data.data.trim() : "");
          break;
      }
    } else {
      this.chooseData.moduleobj.projectIdList.loading(true)
      this.chooseData.moduleobj.taskIdList.loading(true)
      this.chooseData.moduleobj.vendorIdList.loading(true)
      this.taskNameSearch('');
      this.taskVendorNameSearch('');
      this.projectNameSearch('');
    }
  }
  async projectNameSearch(projectName) {
    let { data } = await this.api.projectNameSearch({ projectName ,type:window.location.hash.indexOf('taskmanage')!==-1?1:3})
    this.chooseData.moduleobj.projectIdList.loading(false)
    let projectNames = data.list.map((item) => {
      return {
        'idx': item.projectId,
        'val': item.projectName
      }
    })
    this.chooseData.renderHtml(projectNames, 'projectIdList')
  }
  async taskNameSearch(taskName) {
    let { data } = await this.api.taskNameSearch({ taskName,type:window.location.hash.indexOf('taskmanage')!==-1?1:3 })
    this.chooseData.moduleobj.taskIdList.loading(false)
    let taskNames = data.list.map((item) => {
      return {
        'idx': item.taskId,
        'val': item.taskName
      }
    })
    this.chooseData.renderHtml(taskNames, 'taskIdList')
  }
  async taskVendorNameSearch(vendorName) {
    let { data } = await this.api.taskVendorNameSearch({ vendorName })
    this.chooseData.moduleobj.vendorIdList.loading(false)
    let taskVendorNames = data.list.map((item) => {
      return {
        'idx': item.vendorId,
        'val': item.vendorName
      }
    })
    this.chooseData.renderHtml(taskVendorNames, 'vendorIdList')
  }
  initscroll() {
    if (this.myScroll) {
      this.myScroll.refresh()
      return
    }
    var rid = 'aaa_' + Math.floor(new Date().getTime() * Math.random())
    ES.selctorDoc('.scrolltable').attr('id', rid)
    this.myScroll = new IScroll('#' + rid, {
      scrollbars: true,
      mouseWheel: true,
      probeType: 2,
      interactiveScrollbars: true,
      hideScrollbar: false,
      vScrollbar: true,
      shrinkScrollbars: 'scale',
      fadeScrollbars: false,
      disableMouse: true,
      disablePointer: true
    });
    this.myScroll.on('scroll', () => {
      let temph = ES.selctorDoc('.scrolltable').box().clientHeight
      let mainh = ES.selctorDoc('.maintable').box().clientHeight
      temph = temph > mainh ? mainh : temph
      this.dom.find('.iScrollHorizontalScrollbar').css({
        top: temph - this.y - 8
      })
    })
  }
  inittable() {
    if (this.myScroll1) {
      this.myScroll1.refresh()
      return
    }
    var rid = 'aaa_' + Math.floor(new Date().getTime() * Math.random())
    ES.selctorDoc('.maintable').attr('id', rid)
    this.myScroll1 = new IScroll('#' + rid, {
      scrollbars: true,
      mouseWheel: true,
      scrollX: true,
      interactiveScrollbars: true,
      hideScrollbar: false,
      vScrollbar: true,
      shrinkScrollbars: 'scale',
      fadeScrollbars: false,
      disableMouse: true,
      disablePointer: true
    });
  }
  resize() {
    let ch = ES.selctorDoc(window).box().clientHeight - 100
    let cw = ES.selctorDoc(window).box().clientWidth - 240
    ES.selctorDoc('.taskmanage').css({
      'height': ch,
      'width': cw
    });
    ES.selctorDoc('.scrolltable').css({
      'height': ch - 260
    });
  }
}
module.exports = taskmanage;



